@import 'mixins.scss';
@import 'colors.scss';
/* ANIMAÇÕES */
@keyframes color-glow {
    0% { color: $olivine; }
    50% { color: $gold; }
    100% { color: $olivine; }
}
@-webkit-keyframes fadeIn{
	from { opacity: 0; }to{ opacity: 1; }
}
@keyframes fadeIn {
  	from { opacity: 0; }to{ opacity: 1; }
}
.fadeIn { -webkit-animation-name: fadeIn; animation-name: fadeIn; }
//============================================
@-webkit-keyframes fadeInDown{
	from{
    	opacity: 0;
    	-webkit-transform: translate3d(0, -100%, 0); transform: translate3d(0, -100%, 0);
  	}
  	to{
    	opacity: 1;
    	-webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
   	}
}
@keyframes fadeInDown{
  	from{
    	opacity: 0;
    	-webkit-transform: translate3d(0, -100%, 0); transform: translate3d(0, -100%, 0);
  	}
  	to{
    	opacity: 1;
    	-webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
  	}
}
.fadeInDown{
	-webkit-animation-name: fadeInDown; animation-name: fadeInDown;
}
//============================================
@-webkit-keyframes fadeInUp{
  	from{
    	opacity: 0; -webkit-transform: translate3d(0, 100%, 0); transform: translate3d(0, 100%, 0);
  	}
  	to{
  		opacity: 1; -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
	}
}
@keyframes fadeInUp{
  	from{
    	opacity: 0; -webkit-transform: translate3d(0, 50px, 0); transform: translate3d(0, 50px, 0);
  	}
  	to{
    	opacity: 1; -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
  	}
}
.fadeInUp{ -webkit-animation-name: fadeInUp; animation-name: fadeInUp; transition-duration: 200ms ease-out; }
//============================================
@keyframes fadeInRight {
    from {
        opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
    }to{
        opacity: 1; -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
    }
}
.fadeInRight { -webkit-animation-name: fadeInRight; animation-name: fadeInRight; }
//============================================
@keyframes fadeInLeft{
    from {
        opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
    }to{
        opacity: 1; -webkit-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0);
    }
}

.fadeInLeft { -webkit-animation-name: fadeInLeft; animation-name: fadeInLeft; }
//============================================
@keyframes fadeOut {
    from {
        opacity: 1;
    }to{
        opacity: 0;
    }
}
.fadeOut { -webkit-animation-name: fadeOut; animation-name: fadeOut; }
//============================================
@keyframes fadeOutDown {
    from{
        opacity: 1;
    }to{
        opacity: 0; -webkit-transform: translate3d(0, 100%, 0); transform: translate3d(0, 100%, 0);
    }
}
.fadeOutDown{ -webkit-animation-name: fadeOutDown; animation-name: fadeOutDown; }
//============================================
@keyframes fadeOutLeft{
    from{
        opacity: 1;
    }to{
        opacity: 0; -webkit-transform: translate3d(-100%, 0, 0); transform: translate3d(-100%, 0, 0);
    }
}
.fadeOutLeft{ -webkit-animation-name: fadeOutLeft; animation-name: fadeOutLeft; }
//============================================
@keyframes fadeOutRight {
    from{
        opacity: 1;
    }to{
        opacity: 0; -webkit-transform: translate3d(100%, 0, 0); transform: translate3d(100%, 0, 0);
    }
}
.fadeOutRight{ -webkit-animation-name: fadeOutRight; animation-name: fadeOutRight; }
//============================================
.animate{
    -webkit-animation-duration: 1s; animation-duration: 1s;
	-webkit-animation-fill-mode: both; animation-fill-mode: both;
}
.animate.infinite {
    -webkit-animation-iteration-count: infinite; animation-iteration-count: infinite;
}
.animate.delay-500ms{
    -webkit-animation-delay: 500ms; animation-delay: 500ms;
}
.animate.delay-1s{
    -webkit-animation-delay: 1s; animation-delay: 1s;
}
.animate.delay-1500ms{
    -webkit-animation-delay: 1.5s; animation-delay: 1.5s;
}
.animate.delay-2s{
    -webkit-animation-delay: 2s; animation-delay: 2s;
}
.animate.delay-2500ms{
    -webkit-animation-delay: 2.5s; animation-delay: 2.5s;
}
.animate.delay-3s{
    -webkit-animation-delay: 3s; animation-delay: 3s;
}
.animate.delay-4s{
    -webkit-animation-delay: 4s; animation-delay: 4s;
}
.animate.delay-5s{
    -webkit-animation-delay: 5s; animation-delay: 5s;
}
.animate.fast{
    -webkit-animation-duration: 800ms; animation-duration: 800ms;
}
.animate.faster{
    -webkit-animation-duration: 500ms; animation-duration: 500ms;
}
.animate.slow{
    -webkit-animation-duration: 2s; animation-duration: 2s;
}
.animate.slower{
    -webkit-animation-duration: 3s; animation-duration: 3s;
}
@media (prefers-reduced-motion) {
  	.animate{
	    -webkit-animation: unset !important;
        animation: unset !important;
	    -webkit-transition: none !important;
        transition: none !important;
  	}
}
@-webkit-keyframes pulse {
    0% { -webkit-transform: scale(1); }
    50% { -webkit-transform: scale(1.02); }
    100% { -webkit-transform: scale(1); }
}

@keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.02); }
    100% { transform: scale(1); }
}
/*==========================================*/
